.nav{
    position: fixed;
    display: flex;
    box-sizing: border-box;
    padding: 15px;
    justify-content: space-between;
    height: 60px;
    width: 100%;

    /* Animation */

    transition-timing-function: ease-in;
    transition: all 0.5s;
}

.nav__black{
    background-color: black;
}

.nav__logo{
    position: fixed;
    width: 80px;
    object-fit: contain;
}

.nav__avatar{
    position: fixed;
    right: 20px;
    width: 30px;
    object-fit: contain;
}