.banner{
    color: white;
    object-fit: contain;
    height: 550px;
}

.banner__content{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner__title{
   font-size: 3rem;
   font-weight: 800;
   padding-bottom: 0.3rem; 
}

.banner__description{
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1.09rem;
    max-width: 500px;
    height: 100px;
}

.banner__button{
    cursor: pointer;
    outline: none;
    color: #fff;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);    
}

.banner__button:hover{
    color: black;
    background-color: #e6e6e6;
    transition: all 0.2s;
}

.banner--fadeBottom{
    height: 14.4rem;
    background-image: linear-gradient(180deg, transparent, rgba(37,37,37,0.61),#111);
}
